.chart-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    /* Space between charts */
    width: 100%;
    /* Ensure it takes up the full width */

}

.chart-wrapper {
    flex-grow: 1;
    /* Ensure both charts take equal space */
    max-width: 48%;
    /* Adjust the width to fit both charts in one row */
}